import React from 'react';
import { string, func } from 'prop-types';

import projects from '../data';
import images from '../img/projects';
import ProjectCard from '../components';

const Projects = ({ title, type }) => {
  setTimeout(type, 100);
  return (
    <main>
      <h1>
        {title}
      </h1>
      <section>
        {projects.map((project, i) => (
          <ProjectCard project={project} img={images[i]} key={project.title} />
        ))}
      </section>
    </main>
  );
};

Projects.propTypes = { title: string.isRequired, type: func.isRequired };

export default Projects;
