import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';

import { Home, Contact, Projects } from '../scenes';

class Main extends Component {
  state = { title: '' }

  type = (str) => {
    const { state: { title, title: { length } } } = this;
    if (title !== str) {
      this.setState({
        title: str.substr(0, str.charAt(length) === ' ' ? length + 2 : length + 1),
      });
    }
  };

  render = () => {
    const { type, state: { title } } = this;
    return (
      <Switch>
        <Route
          exact
          path="/"
          render={() => <Home title={title} type={() => type('Evan Brierton')} />}
        />
        <Route
          exact
          path="/projects"
          render={() => <Projects title={title} type={() => type('Projects')} />}
        />
        <Route
          exact
          path="/contact"
          render={() => <Contact title={title} type={() => type('Contact')} />}
        />
      </Switch>
    );
  }
}


export default Main;
